<template>
  <div class="order">
      <div class="head w10">
        <a class="category fl">
          <i class="iconfont icon-fanhui"></i>
        </a>
        <a class="msg"></a>
      </div>
      <div class="content">
        <div class="address">
          <p class="concat">
            <span></span>
            <span>13800138000</span>
          </p>
          <i class="iconfont icon-jiantouyou fr"></i>
          <p class="text">
            18A3503
          </p>
        </div>
        <div><img class="botcss" src="../../assets/images/bottom.png" height="1" width="400"/></div>
        <div class="shop">
          <h3 class="shop-t"><i class="iconfont icon-jiantouyou"></i></h3>
          <div class="panel-img">
            <img src="../../assets/images/shop-img.png" height="80" width="80"/>
            <div class="explain">
              <p>
                
              </p>
              <span>:&nbsp;500g</span>
              <p class="price">140.00</p>
            </div>
          </div>
          <div class="panel-count">
            <div class="p-t clearfix">
              <span class="txt fl"></span>
              <div class="tally fr">
                <button class="sub">-</button><input type="text" name="" value="0"><button class="add">+</button>
              </div>
            </div>
            <div class="fare clearfix">
              <span class="txt fl"></span>
              <span class="txt fr">$0.00</span>
            </div>
          </div>
        </div>
        <div class="act">
          <div class="act-j clearfix">
            <span class="fl"></span>
            <span class="fr"></span>
          </div>
          <div class="act-f clearfix">
            <span class="fl"><i>200</i></span>
            <span class="cked fr"></span>
          </div>
        </div>
        <div class="issue clearfix">
            <span class="fl"></span>
            <i class="iconfont icon-jiantouyou fr"></i>
        </div>
        <div class="settle">
            <p class="clearfix">
              <span class="fl"></span>
              <i class="fr">145.00</i>
            </p>        
            <p class="clearfix">
              <span class="fl"></span>
              <i class="fr">145.00</i>
            </p>        
            <p class="clearfix">
              <span class="fl"></span>
              <i class="fr">145.00</i>
            </p>        
            <p class="clearfix">
              <span class="fl"></span>
              <i class="fr">145.00</i>
            </p>        
            <p class="clearfix">
              <span class="fl"></span>
              <i class="fr">145.00</i>
            </p>
        </div>
      </div>
      <div class="foot w10 clearfix">
        <p class="category fl">
            <span>:</span>
            <i>145.00</i>
        </p>
        <button class="fr" type="button"></button>
      </div>
  </div>
</template>

<script>
    export default {
        name: "order"
    }
</script>

<style scoped>
  @import '../../assets/css/order/order.css';
</style>



